import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  state = {
    //선언을 꼭해야 하는 필수사항이 아님
    //처음부터 선언할 필요 없음
    isLoading: true,
    movies: [],
  };

  //axios는 오래걸리는 건데 componentDidMount()가 이보다 먼저 끝나는 걸 방지하기 위해
  //시간을 벌기 위해 async와 await을 넣어줌
  getMovies = async () => {
    const {
      data: {
        data: { movies },
      },
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json");

    //setState의 movies와 axios에서 온 movies를 알아야 한다
    //this.setState({ movies: movies })
    this.setState({ movies, isLoading: false });
  };
  componentDidMount() {
    this.getMovies();
  }
  render() {
    const { isLoading, movies } = this.state; //원소 선언해야 간단하게 적을 수 있음
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map((movie) => (
              <Movie
                key={movie.id}
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default App;
